import { SortDirection } from './constants';
export const normalizeColumns = (columns, sortable) => {
    const sList = sortable.map((s) => {
        if (typeof s === 'string') {
            return { field: s, direction: undefined };
        }
        return s;
    });
    return columns.map((c) => {
        let id;
        let title;
        let sortDir;
        if (typeof c === 'string') {
            id = c;
            title = c;
            sortDir = SortDirection.ASC;
        }
        else {
            id = c.id;
            title = c.title || c.id;
            sortDir = c.sortDir || SortDirection.ASC;
        }
        const sort = sList.find((s) => (s.field === id));
        if (sort) {
            return {
                id,
                title,
                sortable: true,
                direction: sort.direction || sortDir,
            };
        }
        return {
            id,
            title,
            sortable: false,
            direction: SortDirection.ASC,
        };
    });
};
export const applyAttrributes = (node, attrs) => {
    Object.keys(attrs).forEach((name) => {
        node.setAttribute(name, attrs[name]);
    });
};
